import axios from 'axios';
import { BigNumber, ethers } from 'ethers';
import { useState, useCallback, useEffect } from 'react';
import { CONTRACT_NAME } from '../constants/addresses';
import { useNameservice } from '../store/nameservice-context';

export const useGetDrawResults = () => {
  const [drawResults, setDrawResults] = useState([]);
  const addresses = useNameservice();

  const getResults = useCallback(async () => {
    let data = [];
    try {
      const countRes = await axios.get(`https://api2.zebec.io/givings/count?type=A`);
      if (countRes.status !== 200) {
        throw new Error('Error read count');
      }
      const { count } = countRes.data;
      if (Number(count) <= 0) {
        throw new Error('no draw result');
      }
      const resultsRes = await Promise.all(
        [...Array(Number(count)).keys()].map((item) =>
          axios.get(`https://api2.zebec.io/givings?times=${item}&type=A`),
        ),
      );
      const error = resultsRes.find((item) => item.status !== 200);
      if (error) {
        throw new Error('Error read results');
      }
      data = resultsRes.map((item) => item.data);
    } catch (error) {
      console.log(error);
    }
    return data;
  }, []);

  useEffect(() => {
    if (Object.keys(addresses).length) {
      getResults()
        .then((res) => {
          // create records based on usdt list
          const combinedRecord = res.map((resItem) => {
            const usdtList = resItem.find(
              (item) => item[0].toLowerCase() === addresses[CONTRACT_NAME.USDT].toLowerCase(),
            )[1];
            const busdList = resItem.find(
              (item) => item[0].toLowerCase() === addresses[CONTRACT_NAME.BUSD].toLowerCase(),
            )[1];

            return usdtList.map((usdtItem, index) => {
              const matchingBusdRecord = busdList.find(
                (busdItem) => usdtItem.address === busdItem.address,
              );
              return {
                rank: index + 1,
                address: usdtItem.address,
                usdtAmount: BigNumber.from(usdtItem.amount),
                timestamp: Number(usdtItem.timestamp),
                busdAmount: BigNumber.from(matchingBusdRecord.amount),
              };
            });
          });
          combinedRecord.push([
            {
              rank: 1,
              address: '-',
              usdtAmount: ethers.constants.Zero,
              timestamp: 0,
              busdAmount: ethers.constants.Zero,
            },
            {
              rank: 2,
              address: '-',
              usdtAmount: ethers.constants.Zero,
              timestamp: 0,
              busdAmount: ethers.constants.Zero,
            },
            {
              rank: 3,
              address: '-',
              usdtAmount: ethers.constants.Zero,
              timestamp: 0,
              busdAmount: ethers.constants.Zero,
            },
          ]);
          setDrawResults(combinedRecord);
        })
        .catch((error) => console.log(error));
    }
  }, [getResults, addresses]);
  return drawResults;
};
