import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { useCountdown } from '../../../hooks/useCountdown';
import FlipClockCard from './FlipClockCard/FlipClockCard';

import styles from './FlipClock.module.scss';

const FlipClock = (props) => {
  const { endTime } = props;
  const [days, hours, minutes, seconds] = useCountdown(endTime);
  const { t } = useTranslation();
  return (
    <div className={styles.FlipClock}>
      {!!days && <FlipClockCard currTime={days} title={t('Days', 'Days')} />}
      <FlipClockCard currTime={hours} title={t('Hours', 'Hours')} />
      <FlipClockCard currTime={minutes} title={t('Minutes', 'Minutes')} />
      <FlipClockCard currTime={seconds} title={t('Seconds', 'Seconds')} />
    </div>
  );
};

FlipClock.propTypes = {
  endTime: PropTypes.number.isRequired,
};

export default FlipClock;
