import moment from 'moment';
import numeral from 'numeral';
import React from 'react';
import { BigNumber, ethers } from 'ethers';
import { formatEther } from 'ethers/lib/utils';
import { useTranslation } from 'react-i18next';

import { useGetLatestParticipants } from '../../../hooks/useGetLatestParticipants';
import { useTreasuryRewardsAmount } from '../../../hooks/useTreasuryRewardsAmount';
// import { useVaultBalance } from '../../../hooks/useToken';
import { DAY } from '../../../utils/utils';
import FlipClock from '../../UI/FlipClock/FlipClock';

import styles from './Treasury.module.scss';

const Treasury = () => {
  const { t } = useTranslation();

  const data = useTreasuryRewardsAmount();
  const fomoPoolSum = data.fomoPoolSum || BigNumber.from('0');
  const arr = numeral(
    formatEther(fomoPoolSum.gt(ethers.constants.WeiPerEther) ? fomoPoolSum : BigNumber.from('0')),
  )
    .format('00000000')
    .split('');
  const latestParticipants = useGetLatestParticipants();

  return (
    <div className={styles.Treasury}>
      <div className={styles.TreasuryHead}>
        <img src="/static/images/Participate/treasuryHead.svg" alt="treasury" />
        <span>{t('Horizon Treasury', 'Horizon Treasury')}</span>
      </div>

      <div className={styles.CountDown}>
        <FlipClock
          endTime={latestParticipants?.[0] ? (latestParticipants[0].timestamp + DAY) * 1000 : 0}
        />
      </div>

      <div className={styles.TreasuryTitle}>
        {t('TOTAL BONUS REMAIN', 'TOTAL BONUS REMAIN')}
        {/* <Tooltip>
          <QuestionCircleFilled />
        </Tooltip> */}
      </div>

      <div className={styles.TreasuryValue}>
        {arr.map((v, i) => (
          // eslint-disable-next-line
          <div className={styles.TreasuryValueItem} key={i}>
            <span>{v}</span>
          </div>
        ))}
      </div>
      <div className={styles.TreasuryDesc}>
        {t(
          'treasury-desc',
          'If no new node is created within 24 hours, 20% of the total bonus will be rewarded to the last node participant, and 80% will be equally distributed to the last 2-100 participants.',
        )}
      </div>
      {latestParticipants?.[0] && (
        <div className={styles.LastSold}>
          <img src="/static/images/RewardsPool/speaker.svg" alt="speaker" />{' '}
          {t('The latest Zepoch was sold', 'The latest Zepoch was sold')}{' '}
          <span>{moment(latestParticipants[0].timestamp * 1000).fromNow()}</span>
        </div>
      )}
    </div>
  );
};

export default Treasury;
