import { useCallback, useEffect } from 'react';
import { BigNumber, ethers } from 'ethers';
import { useBinanceUsdDaoVaultContract, useContract } from './useContract';
import { useUpdate } from './useUpdate';
import { useTokenBalance } from './useToken';
import { BINANCE_USD_DAO_VAULT_ADDR } from '../constants/addresses';

export const useDaoVaultStats = (tokenAddress) => {
  const daoVaultContract = useBinanceUsdDaoVaultContract();
  const { updateByTimer } = useUpdate();

  const decode = useCallback(
    (records) =>
      records.map((item, index) => ({
        key: index,
        tokenAddress: item.tokenAddress,
        from: item.from,
        amount: item.amount,
        zbcBurned: item.zbcBurned,
        timestamp: item.timestamp,
        type: 'Buyback',
        price: BigNumber.from(item.amount)
          .mul(10 ** 9)
          .div(2)
          .div(item.zbcBurned),
      })),
    [],
  );

  const { state: cumulativeErc20DepositedState, read: readCumulativeErc20Deposited } = useContract(
    daoVaultContract,
    'cumulativeErc20Deposited',
    null,
    ethers.constants.Zero,
  );
  const { state: cumulativeZbcBurnState, read: readCumulativeZbcBurn } = useContract(
    daoVaultContract,
    'cumulativeZbcBurn',
    null,
    ethers.constants.Zero,
  );
  const { state: depositBurnRecordState, read: readDepositBurnRecord } = useContract(
    daoVaultContract,
    'depositBurnRecord',
    decode,
    [],
  );
  const erc20Balance = useTokenBalance(tokenAddress, 18, BINANCE_USD_DAO_VAULT_ADDR);

  useEffect(() => {
    const fetchData = () => {
      if (!ethers.utils.isAddress(tokenAddress) || !daoVaultContract) {
        return;
      }
      readCumulativeErc20Deposited([tokenAddress]);
      readCumulativeZbcBurn();
      readDepositBurnRecord();
    };
    fetchData();
  }, [
    updateByTimer,
    readCumulativeErc20Deposited,
    readCumulativeZbcBurn,
    readDepositBurnRecord,
    tokenAddress,
    daoVaultContract,
  ]);

  return {
    cumulativeErc20Deposited: cumulativeErc20DepositedState.data,
    cumulativeErc20Spend: cumulativeErc20DepositedState.data.div(2),
    cumulativeZbcBurn: cumulativeZbcBurnState.data,
    erc20Balance: erc20Balance.data,
    depositBurnRecord: depositBurnRecordState.data,
  };
};

export const useDepositErc20 = () => {
  const daoVaultContract = useBinanceUsdDaoVaultContract();
  const { state, write } = useContract(daoVaultContract, 'depositErc20');

  const depositErc20 = async (tokenAddress, amount) => {
    if (!(ethers.utils.isAddress(tokenAddress) && amount.gt(0))) {
      return;
    }
    await write([tokenAddress, amount]);
  };
  return { state, depositErc20 };
};
