import { useMemo } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import About from '../views/About/About';
import Participate from '../views/Participate/Participate';
import Points from '../views/Points/Points';
import Profile from '../views/Profile/Profile';
import LearnMore from '../views/LearnMore/LearnMore';
import Airdrop from '../views/Airdrop/Airdrop';
import Rewards from '../views/Rewards/Rewards';
import Swap from '../views/Swap/Swap';
import RewardsPool from '../views/RewardsPool/RewardsPool';
import Dao from '../views/Dao/Dao';

export const usePages = () => {
  const {
    i18n: { resolvedLanguage },
  } = useTranslation();

  const bridgeMenuItems = useMemo(
    () => [
      {
        key: '1',
        label: (
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://www.portalbridge.com/#/transfer">
            Portal Bridge
          </a>
        ),
      },
      {
        key: '2',
        label: (
          <a
            target="_blank"
            rel="noopener noreferrer"
            href={
              resolvedLanguage === 'zh'
                ? 'https://horizon.zebec.io/docs/bsc-solana-bridge-cn.pdf'
                : 'https://medium.com/zebec-protocol/how-to-bridge-zbc-from-solana-to-binance-smart-chain-via-portal-token-bridge-and-vice-versa-ccddfdaa1aa5'
            }>
            <Trans i18nKey="Bridge Guide">Bridge Guide</Trans>
          </a>
        ),
      },
    ],

    [resolvedLanguage],
  );

  const pages = useMemo(
    () => [
      { id: 0, text: 'About', path: '/about', component: About },
      { id: 1, text: 'Participate', path: '/participate', component: Participate },
      { id: 2, text: 'Zebec Points', path: '/points', component: Points },
      { id: 3, text: 'Airdrop', path: '/airdrop', component: Airdrop },
      { id: 4, text: 'Swap', path: '/swap', component: Swap },
      { id: 5, text: 'Rewards Plan', path: '/rewards', component: Rewards },
      { id: 6, text: 'Treasury', path: '/Treasury', component: RewardsPool },
      { id: 7, text: 'DAO', path: '/dao', component: Dao },
      { id: 8, text: 'Profile', path: '/profile', component: Profile, protected: true },
      { id: 9, text: 'Docs', href: 'https://docs.zebec.io/operation-horizon/' },
      { id: 10, text: 'Bridge', menuItems: bridgeMenuItems },
      { id: 11, text: 'LearnMore', path: '/learnMore', component: LearnMore, isSubPage: true },
    ],
    [bridgeMenuItems],
  );
  return pages;
};
