import React, { useEffect, useState } from 'react';
import { List } from 'antd';
import { useTranslation } from 'react-i18next';
import { useGeolocation } from '../../hooks/useGeolocation';
import CustomModal from '../UI/CustomModal/CustomModal';
import styles from './KYCModal.module.scss';
import CustomButton from '../UI/CustomButton/CustomButton';

const FORBIDDEN_COUNTRY_BY_IP = ['US'];
const FORBIDDEN_COUNTRY = [
  'Myanmar',
  "Cote D'lvoire",
  'Cuba',
  'Congo',
  'Congo (D.R.C)',
  'Iran',
  'North Korea',
  'Sudan',
  'Syria',
  'Afghanistan',
  'Russian Federation',
  'China',
  'Ukraine',
  'Belarus',
  'United States',
  'American Samoa',
  'Guam',
  'Northern Mariana Islands',
  'Puerto Rico',
  'Virgin Islands, U.S.',
  'United States Minor Outlying Islands',
];

const KYCModal = () => {
  const [modalVisible, setModalVisible] = useState(false);
  const geolocation = useGeolocation();
  const { t } = useTranslation();

  useEffect(() => {
    if (FORBIDDEN_COUNTRY_BY_IP.includes(geolocation.country)) {
      setModalVisible(true);
    }
  }, [geolocation]);

  return (
    <CustomModal
      open={modalVisible}
      onCancel={() => {
        setModalVisible(false);
      }}>
      <div className={styles.KYCModalContent}>
        <h2>{t('KYC title', 'Zebec Service Not Available')}</h2>
        <p>
          {t(
            'KYC desc',
            'For regulatory reasons, Zebec does not offer service in the following jurisdictions:',
          )}
        </p>
        <List
          style={{ width: '100%' }}
          size="small"
          dataSource={FORBIDDEN_COUNTRY}
          renderItem={(item) => (
            <List.Item
              style={{
                justifyContent: 'center',
                fontWeight: '500',
                fontSize: '14px',
                color: 'rgba(0,0,0,0.8)',
                padding: '3px',
              }}>
              {item}
            </List.Item>
          )}
        />
        <CustomButton
          onClick={() => {
            setModalVisible(false);
          }}>
          {t('KYC not apply', 'This Does Not Apply to Me')}
        </CustomButton>
      </div>
    </CustomModal>
  );
};

export default KYCModal;
