import React from 'react';
import { ethers } from 'ethers';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react';
import { formatEther } from 'ethers/lib/utils';
import { useTranslation } from 'react-i18next';

import { useGetDrawResults } from '../../../hooks/useGetDrawResults';
import { formatNumber, truncAddress } from '../../../utils/utils';
import { useIsMobileView } from '../../../hooks/useIsMobileView';
import TitleCombo from '../../UI/TitleCombo/TitleCombo';
import SwiperControl from '../SwiperControl/SwiperControl';
import { useTreasuryRewardsAmount } from '../../../hooks/useTreasuryRewardsAmount';

import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.min.css';
import styles from './DrawResults.module.scss';

const DrawResults = () => {
  const { t } = useTranslation();
  const drawResults = useGetDrawResults();
  const isMobile = useIsMobileView();
  const { drawPoolSum } = useTreasuryRewardsAmount();

  return (
    <div className={styles.DrawResults}>
      <TitleCombo small className={styles.resultTitle} white>
        <h2>{t('Draw Pool', 'Draw Pool')}</h2>
        <p>
          {t(
            'Draw Pool Desc',
            'Three winners are drawn from the participants for every 500 Zepoch Nodes sold.',
          )}
        </p>
      </TitleCombo>
      <Swiper
        spaceBetween={25}
        slidesPerView={isMobile ? 1 : 3}
        centerInsufficientSlides
        grabCursor>
        {drawResults.map((roundResult, index) => {
          const roundSum = roundResult.reduce(
            (accum, item) => accum.add(item.usdtAmount).add(item.busdAmount),
            ethers.constants.Zero,
          );
          return (
            // eslint-disable-next-line
            <SwiperSlide className={styles.slide} key={index}>
              <div className={styles.slideHeaderWrap}>
                <div className={styles.slideHeader}>Round {index + 1}</div>
                <div className={styles.rewardTotal}>
                  <img src="/static/images/RewardsPool/coins.svg" alt="coins" />{' '}
                  {formatNumber(
                    formatEther(
                      roundSum.gt(ethers.constants.Zero)
                        ? roundSum
                        : drawPoolSum ?? ethers.constants.Zero,
                    ),
                    '$0,0.[00]',
                  )}
                </div>
              </div>

              <h2 className={styles.title}>{t('Winners', 'Winners')}</h2>

              <div className={styles.result}>
                {roundResult.map((item) => (
                  <div className={styles.resultItem} key={item.rank}>
                    <h2>
                      {item.rank}. {truncAddress(item.address)}
                    </h2>
                    <p>
                      {formatNumber(formatEther(item.usdtAmount.add(item.busdAmount)), '$0,0.[00]')}
                    </p>
                  </div>
                ))}
              </div>
            </SwiperSlide>
          );
        })}
        <SwiperControl />
      </Swiper>
    </div>
  );
};

export default DrawResults;
