import React, { useState } from 'react';
import { message } from 'antd';
import { parseEther } from 'ethers/lib/utils';
import { ethers } from 'ethers';
import { useTranslation } from 'react-i18next';

import { DAO_MANAGER_ADDR } from '../../../constants/misc';
import { useWallet } from '../../../store/wallet-context';
import Card from '../../UI/Card/Card';
import CustomInput from '../../UI/CustomInput/CustomInput';
import styles from './DaoManagement.module.scss';
import CustomButton from '../../UI/CustomButton/CustomButton';
import { formatNumber } from '../../../utils/utils';
import { useAllTokenAllowance, useAllTokenBalance, useTokenApprove } from '../../../hooks/useToken';
import { BINANCE_USD_DAO_VAULT_ADDR, DAO_DEPOSIT_TOKEN } from '../../../constants/addresses';
import { useNameservice } from '../../../store/nameservice-context';
import { useDepositErc20 } from '../../../hooks/useBinanceUsdDaoVault';

const DaoManagement = () => {
  const { t } = useTranslation();
  const [depositAmount, setDepositAmount] = useState('');
  const { account } = useWallet();

  const addresses = useNameservice();
  const { state: approveTokenState, approve } = useTokenApprove(addresses[DAO_DEPOSIT_TOKEN]);
  const { state: depositErc20State, depositErc20 } = useDepositErc20(addresses[DAO_DEPOSIT_TOKEN]);

  const allTokenAllowance = useAllTokenAllowance(BINANCE_USD_DAO_VAULT_ADDR);
  const { data: usdtAllowance } = allTokenAllowance[DAO_DEPOSIT_TOKEN];

  const allTokenBalance = useAllTokenBalance();
  const { data: usdtBalance } = allTokenBalance[DAO_DEPOSIT_TOKEN];
  const { data: nativeBalance } = allTokenBalance.nativeBalance;

  if (
    !(
      ethers.utils.isAddress(account) &&
      DAO_MANAGER_ADDR.find((item) => item.toLocaleLowerCase() === account.toLocaleLowerCase())
    )
  ) {
    return null;
  }

  const handleDepositAmountChange = (e) => {
    const {
      target: { value },
    } = e;
    if (value.length === 0) {
      setDepositAmount(value);
      return;
    }
    try {
      parseEther(value);
      setDepositAmount(value);
    } catch (error) {
      console.log('invalid input');
    }
  };

  const handleApprove = () => {
    if (approveTokenState.isLoading || !ethers.utils.isAddress(BINANCE_USD_DAO_VAULT_ADDR)) {
      return;
    }
    if (!account) {
      message.info(t('Please connect to wallet', 'Please connect to wallet'));
      return;
    }
    if (nativeBalance < 0.01) {
      message.info(
        t(
          "You don't have enough BNB for gas to complete the transaction",
          "You don't have enough BNB for gas to complete the transaction",
        ),
      );
      return;
    }
    try {
      approve(BINANCE_USD_DAO_VAULT_ADDR);
    } catch (error) {
      console.log(error);
    }
  };

  const handleDeposit = async () => {
    if (depositErc20State.isLoading && usdtBalance && addresses[DAO_DEPOSIT_TOKEN]) {
      return;
    }
    if (!account) {
      message.info(t('Please connect to wallet', 'Please connect to wallet'));
      return;
    }
    if (nativeBalance < 0.01) {
      message.info(
        t(
          "You don't have enough BNB for gas to complete the transaction",
          "You don't have enough BNB for gas to complete the transaction",
        ),
      );
      return;
    }
    if (!depositAmount) {
      message.info(t('Please enter a valid amount', 'Please enter a valid amount'));
      return;
    }
    const amount = parseEther(depositAmount);
    if (amount.gt(parseEther(usdtBalance.toString()))) {
      message.info(
        t('Not enough currency', 'Not enough {{currency}}', { currency: DAO_DEPOSIT_TOKEN }),
      );
      return;
    }
    try {
      await depositErc20(addresses[DAO_DEPOSIT_TOKEN], amount);
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <Card className={styles.DaoManagement}>
      <h2 className={styles.cardTitle}>Dao Management</h2>
      <div className={styles.actionItem}>
        <div className={styles.inputWrap}>
          <div className={styles.balance}>
            <p>Balance</p>
            <p>{formatNumber(usdtBalance)}</p>
          </div>
          <div className={styles.input}>
            <CustomInput
              placeholder="Deposit"
              value={depositAmount}
              onChange={handleDepositAmountChange}
            />
            <div className={styles.suffix}>USDT</div>
          </div>
        </div>
        {usdtAllowance ? (
          <CustomButton onClick={handleDeposit} loading={depositErc20State.isLoading}>
            Deposit
          </CustomButton>
        ) : (
          <CustomButton onClick={handleApprove} loading={approveTokenState.isLoading}>
            Approve USDT
          </CustomButton>
        )}
      </div>
    </Card>
  );
};

export default DaoManagement;
