import classNames from 'classnames';
import React from 'react';
import { useSwiper } from 'swiper/react';
import styles from './SwiperControl.module.scss';

const SwiperControl = () => {
  const swiper = useSwiper();

  const handleSlideNext = () => {
    if (swiper.isEnd) {
      return;
    }
    swiper.slideNext();
  };

  const handleSlidePrev = () => {
    if (swiper.isBeginning) {
      return;
    }
    swiper.slidePrev();
  };

  const handleSlide = (e) => {
    const {
      target: { value },
    } = e;
    swiper.slideTo(Number(value));
  };

  return (
    <div className={styles.SwiperControl}>
      <img
        className={styles.arrow}
        alt="prev"
        src="/static/images/RewardsPool/arrowLeft.svg"
        onClick={handleSlidePrev}
      />
      <input
        type="range"
        min={0}
        max={swiper.slides.length ? swiper.slides.length - 1 : 0}
        value={swiper.activeIndex}
        className={styles.progress}
        onChange={handleSlide}
      />
      <img
        className={classNames(styles.arrow, styles.reverse)}
        alt="next"
        src="/static/images/RewardsPool/arrowLeft.svg"
        onClick={handleSlideNext}
      />
    </div>
  );
};

export default SwiperControl;
