import axios from 'axios';
import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useIsMounted } from './useIsMounted';
import { useUpdate } from './useUpdate';

export const useTreasuryRewardsAmount = () => {
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  const [rewardsData, setRewardsData] = useState({});

  const fetchRewards = useCallback(async () => {
    let data = {};

    try {
      const res = await axios.get(`https://api2.zebec.io/balance`);
      if (res.status === 200) {
        data = res.data;
      }
    } catch (error) {
      console.log(error);
    }
    return data;
  }, []);

  useEffect(() => {
    fetchRewards().then((res) => {
      if (!Object.keys(res).length || !isMounted.current) {
        return;
      }
      setRewardsData({
        drawPoolAmountUsdt: BigNumber.from(res.AUSDT),
        drawPoolAmountBusd: BigNumber.from(res.ABUSD),
        drawPoolSum: BigNumber.from(res.AUSDT).add(BigNumber.from(res.ABUSD)),
        fomoPoolAmountUsdt: BigNumber.from(res.BUSDT),
        fomoPoolAmountBusd: BigNumber.from(res.BBUSD),
        fomoPoolSum: BigNumber.from(res.BUSDT).add(BigNumber.from(res.BBUSD)),
        combinedBalance: BigNumber.from(res.SUM),
      });
    });
  }, [fetchRewards, isMounted, updateByTimer]);
  return rewardsData;
};
