import React, { useState, useEffect, useCallback } from 'react';
import { BigNumber, ethers, utils } from 'ethers';
import { Col, message, Row } from 'antd';
import classNames from 'classnames';
import axios from 'axios';
import { useTranslation } from 'react-i18next';

import { useWallet } from '../../store/wallet-context';
import { formatNumber } from '../../utils/utils';
import { useAllTokenBalance } from '../../hooks/useToken';
import CustomButton from '../../components/UI/CustomButton/CustomButton';
import MainContainer from '../../components/UI/MainContainer/MainContainer';
import { useIsMobileView } from '../../hooks/useIsMobileView';
import { useUpdate } from '../../hooks/useUpdate';
import styles from './Airdrop.module.scss';
import common from '../../common.module.scss';
import { useClaimViaVault } from '../../hooks/useManager';

export const AIRDROPS = [
  {
    id: 0,
    bannerImg: '/static/images/Airdrop/banner0.png',
    title: 'airdrop-title-0',
    desc: 'airdrop-desc-0',
    bannerTitle: 'airdrop-banner-title-0',
    bannerSubtitle: 'airdrop-banner-subtitle-0',
  },
  // {
  //   id: 1,
  //   bannerImg: '/static/images/Airdrop/banner0.png',
  //   title: '5 Million ZBC Tokens Giveaway',
  //   desc: '5 Million $ZBC tokens are distributed to the Zepoch holders based on their Zebec Points when we reach 500 #ZepochNode holders',
  //   claimable: 12345,
  //   claimed: 223,
  //   bannerTitle: '5 Million ZBC Tokens Airdrop Incoming!',
  //   bannerSubtitle: '500 Zepoch Node Holder Milestone',
  // },
  // {
  //   id: 2,
  //   bannerImg: '/static/images/Airdrop/banner0.png',
  //   title: '5 Million ZBC Tokens Giveaway',
  //   desc: '5 Million $ZBC tokens are distributed to the Zepoch holders based on their Zebec Points when we reach 500 #ZepochNode holders',
  //   claimable: 12345,
  //   claimed: 223,
  //   bannerTitle: '5 Million ZBC Tokens Airdrop Incoming!',
  //   bannerSubtitle: '500 Zepoch Node Holder Milestone',
  // },
];

const COL_SPAN = {
  span: 24,
};

const COL_SPAN_COMPACT = {
  span: 24,
  sm: 12,
  md: 8,
};

const Airdrop = () => {
  const isMobile = useIsMobileView();
  const { t } = useTranslation();
  const { updateByTimer } = useUpdate();
  const { account } = useWallet();
  const [airdropList, setAirdropList] = useState([]);
  const { state: claimViaVaultState, claimViaVault } = useClaimViaVault();
  const allTokenBalance = useAllTokenBalance();
  const { data: nativeBalance } = allTokenBalance.nativeBalance;
  const airdropLenght = airdropList.length;
  const colSpan = airdropLenght > 2 ? COL_SPAN_COMPACT : COL_SPAN;

  const fetchClaim = useCallback(async () => {
    let data = {};
    if (!utils.isAddress(account)) {
      return data;
    }
    try {
      const res = await axios.get(`https://api2.zebec.io/claim/zebec?address=${account}`);
      if (res.status === 200) {
        data = res.data;
      }
    } catch (error) {
      console.log(error);
    }
    return data;
  }, [account]);

  // get airdrop info
  useEffect(() => {
    if (account) {
      fetchClaim().then((res) => setAirdropList([{ ...AIRDROPS[0], claimData: res }]));
    } else {
      setAirdropList([AIRDROPS[0]]);
    }
  }, [updateByTimer, account, fetchClaim]);

  const handleClaim = async (item) => {
    if (claimViaVaultState.isLoading) {
      return;
    }
    if (!account) {
      message.info(t('Please connect to wallet', 'Please connect to wallet'));
      return;
    }
    if (nativeBalance < 0.01) {
      message.info(
        t(
          "You don't have enough BNB for gas to complete the transaction",
          "You don't have enough BNB for gas to complete the transaction",
        ),
      );
      return;
    }
    if (!item?.claimData?.amount || !BigNumber.from(item.claimData.amount).gt(0)) {
      message.info(t('No ZBC to claim', 'No ZBC to claim'));
      return;
    }
    if (
      ethers.utils.formatBytes32String('ClaimAirdrop') !==
      '0x436c61696d41697264726f700000000000000000000000000000000000000000'
    ) {
      message.info(t('Invalid claim', 'Invalid claim'));
      return;
    }
    try {
      await claimViaVault(item.claimData);
    } catch (error) {
      message.info(t('Claim error', 'Claim error'));
    }
  };

  return (
    <div className={styles.Airdrop}>
      <MainContainer className={styles.mainContent}>
        <Row gutter={[16, 32]}>
          {airdropList.map((item) => (
            // eslint-disable-next-line
            <Col {...colSpan} key={item.id}>
              <div
                className={classNames(styles.airdropItem, {
                  [styles.mobile]: isMobile || airdropLenght > 2,
                })}>
                <div
                  className={styles.bannerWrap}
                  style={{ backgroundImage: `url(${item.bannerImg})` }}>
                  <div className={styles.textWrap}>
                    <h2 className={styles.bannerSubtitle}>{t(item.bannerSubtitle)}</h2>
                    <h2 className={classNames(common.gradientTitle, styles.bannerTitle)}>
                      {t(item.bannerTitle)}
                    </h2>
                  </div>
                  <div to="/" className={styles.logo}>
                    <img src="/static/images/Header/logo.svg" alt="zebec" />
                    <h2>Zebec</h2>
                  </div>
                </div>
                <div className={styles.contentWrap}>
                  <h2 className={styles.title}>{t(item.title)}</h2>
                  <p className={styles.desc}>{t(item.desc)}</p>
                  <div className={styles.actionWrap}>
                    <div className={styles.infoWrap}>
                      <div className={styles.infoItem}>
                        <h2>{t('Claimable', 'Claimable')}</h2>
                        <p>
                          {formatNumber(
                            item?.claimData?.amount
                              ? utils.formatUnits(item.claimData.amount, 9)
                              : 0,
                          )}{' '}
                          ZBC
                        </p>
                      </div>
                      {/* <div className={styles.infoItem}>
                        <h2>Claimed</h2>
                        <p>{item.claimed} ZBC</p>
                      </div> */}
                    </div>
                    <CustomButton
                      className={styles.button}
                      secondary
                      rounded
                      onClick={() => {
                        handleClaim(item);
                      }}
                      loading={claimViaVaultState.isLoading}>
                      {t('Claim', 'Claim')}
                    </CustomButton>
                  </div>
                </div>
              </div>
            </Col>
          ))}
        </Row>
      </MainContainer>
    </div>
  );
};

export default Airdrop;
