import axios from 'axios';
import { useCallback, useEffect, useState } from 'react';
import { useIsMounted } from './useIsMounted';
import { useUpdate } from './useUpdate';

export const useGetLatestParticipants = () => {
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  const [latestList, setLatestList] = useState([]);

  const getList = useCallback(async () => {
    let data = [];
    try {
      const res = await axios.get(`https://api2.zebec.io/100buys`);
      if (res.status === 200) {
        data = res.data;
      }
    } catch (error) {
      console.log(error);
    }
    return data;
  }, []);

  useEffect(() => {
    getList().then((res) => {
      if (!res.length || !isMounted.current) {
        return;
      }
      setLatestList(res.reverse().map((item, index) => ({ ...item, rank: index + 1 })));
    });
  }, [updateByTimer, getList, isMounted]);
  return latestList;
};
